import {mapState} from 'vuex';
export default {
	data() {
		return {
			inputList: {
				mobile:{						
					value: '',
					type: 'text',
					placeholder: '请输入您的手机号码',
					focus: false,
					icon:"cuIcon-mobilefill"
				},					
				password:{						
					value: '',
					type: 'password',
					placeholder: '请输入密码',
					focus: false,
					showpwd: true,
					icon:"cuIcon-lock"
				}
				

			},
			
			
		}
	},
	methods: {
		focus(e) {
			e.focus = true;
		},
		blur(e) {
			e.focus = false;
		},
		input(e) {
			let index = e.target.dataset.index;
			this.inputList[index].value = e.detail.value;
		},
		clear(e) {
			e.value = '';
		},
		showPwd(e) {
			e.type = e.type == 'password' ? 'text' : 'password';
		},
		btnSave() {
			
			let mobile=this.inputList.mobile.value;
			let password=this.inputList.password.value;
			if (mobile.length < 1) {
				uni.showToast({
					title:"请输入手机号码",
					icon:"none"
				})
				return;
			}
			if (!(/^1[345789]\d{9}$/.test(mobile))){
				 uni.showToast({
					title:"手机格式填写不对",
					icon:'none'
				 })
			}
			if (password.length < 1) {
				uni.showToast({
					title:"请输入密码",
					icon:"none"
				})
				return;
			}
			this.$globalMethod.ds_api({
				method:"m=Y001&token=SemXlsuV0Ko2Ugrp&openId=" +this.$globalMethod.openID+"&publicOpenId="+this.$globalMethod.openID.openWatchId,
				showLoading:false,
				loadmsg:"登录",
				data:{
					mobile:mobile,
					password:password
				},
				success:(res)=>{
					if(!res || !res.data.data){
						return
					}
					let userObj=res.data.data;
					delete userObj.promptMessage;
					userObj.password=password;
					this.$status.login(userObj);
					uni.showToast({
						title:"登录成功",
						icon:"none"
					})
					uni.reLaunch({
						url: '/pages/appointment/index/index',
					});
				}
			})

		},
	},
	computed: {
		...mapState(['$status'])
	},
	
}
